"use client"

import { useState } from "react"
import { Button } from "@/components/ui/button"
import { Card } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table"
import { useRouter } from "next/navigation"

// Mock data for demonstration
const mockReports = [
  { id: "P001", name: "Ahmad bin Abdullah", attendance: 20, late: 2, onTime: 18 },
  { id: "P002", name: "Siti binti Rahman", attendance: 19, late: 1, onTime: 18 },
  { id: "P003", name: "Raj Kumar", attendance: 20, late: 0, onTime: 20 },
]

export default function ReportsPage() {
  const router = useRouter()
  const [dateRange, setDateRange] = useState({
    startDate: "",
    endDate: "",
  })
  const [reports] = useState(mockReports)
  const [loading, setLoading] = useState(false)

  const handleGenerateReport = async (e: React.FormEvent) => {
    e.preventDefault()
    setLoading(true)
    try {
      // In a real app, this would fetch data from an API
      await new Promise(resolve => setTimeout(resolve, 1000)) // Simulate API call
      console.log("Generating report for:", dateRange)
    } catch (error) {
      console.error("Error generating report:", error)
    } finally {
      setLoading(false)
    }
  }

  const handleExportCSV = () => {
    // Create CSV content
    const headers = ["ID Penyelia", "Nama", "Jumlah Kehadiran", "Lewat", "Tepat Masa"]
    const rows = reports.map(report => [
      report.id,
      report.name,
      report.attendance,
      report.late,
      report.onTime
    ])
    
    const csvContent = [
      headers.join(","),
      ...rows.map(row => row.join(","))
    ].join("\n")

    // Create blob and download
    const blob = new Blob([csvContent], { type: "text/csv;charset=utf-8;" })
    const link = document.createElement("a")
    const url = URL.createObjectURL(blob)
    link.setAttribute("href", url)
    link.setAttribute("download", `laporan_kehadiran_${dateRange.startDate}_${dateRange.endDate}.csv`)
    document.body.appendChild(link)
    link.click()
    document.body.removeChild(link)
  }

  const handleExportExcel = () => {
    // In a real app, this would generate an Excel file
    alert("Fungsi muat turun Excel akan datang!")
  }

  const handleExportPDF = () => {
    // In a real app, this would generate a PDF file
    alert("Fungsi muat turun PDF akan datang!")
  }

  const handleLogout = () => {
    router.push("/login")
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-gray-900 to-gray-800">
      <nav className="bg-white/10 backdrop-blur-lg border-b border-gray-700">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex justify-between h-16 items-center">
            <h1 className="text-xl font-bold text-white">Laporan Kehadiran</h1>
            <div className="space-x-4">
              <Button variant="outline" onClick={() => router.push("/dashboard")}>
                Dashboard
              </Button>
              <Button variant="outline" onClick={handleLogout}>
                Log Keluar
              </Button>
            </div>
          </div>
        </div>
      </nav>

      <main className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        <Card className="bg-white/10 backdrop-blur-lg p-6 mb-8">
          <form onSubmit={handleGenerateReport} className="space-y-6">
            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
              <div className="space-y-2">
                <Label className="text-white" htmlFor="startDate">Tarikh Mula</Label>
                <Input
                  id="startDate"
                  type="date"
                  value={dateRange.startDate}
                  onChange={(e) => setDateRange({ ...dateRange, startDate: e.target.value })}
                  className="bg-white/20 text-white"
                  required
                />
              </div>
              <div className="space-y-2">
                <Label className="text-white" htmlFor="endDate">Tarikh Akhir</Label>
                <Input
                  id="endDate"
                  type="date"
                  value={dateRange.endDate}
                  onChange={(e) => setDateRange({ ...dateRange, endDate: e.target.value })}
                  className="bg-white/20 text-white"
                  required
                />
              </div>
            </div>
            <div className="flex flex-wrap gap-4">
              <Button type="submit" disabled={loading}>
                {loading ? "Menjana..." : "Jana Laporan"}
              </Button>
              <Button type="button" variant="outline" onClick={handleExportCSV}>
                Muat Turun CSV
              </Button>
              <Button type="button" variant="outline" onClick={handleExportExcel}>
                Muat Turun Excel
              </Button>
              <Button type="button" variant="outline" onClick={handleExportPDF}>
                Muat Turun PDF
              </Button>
            </div>
          </form>
        </Card>

        <Card className="bg-white/10 backdrop-blur-lg p-6">
          <h2 className="text-2xl font-bold text-white mb-6">Ringkasan Kehadiran</h2>
          <div className="rounded-md border border-gray-700">
            <Table>
              <TableHeader>
                <TableRow className="border-gray-700">
                  <TableHead className="text-white">ID Penyelia</TableHead>
                  <TableHead className="text-white">Nama</TableHead>
                  <TableHead className="text-white">Jumlah Kehadiran</TableHead>
                  <TableHead className="text-white">Lewat</TableHead>
                  <TableHead className="text-white">Tepat Masa</TableHead>
                </TableRow>
              </TableHeader>
              <TableBody>
                {reports.map((report) => (
                  <TableRow key={report.id} className="border-gray-700">
                    <TableCell className="text-white">{report.id}</TableCell>
                    <TableCell className="text-white">{report.name}</TableCell>
                    <TableCell className="text-white">{report.attendance}</TableCell>
                    <TableCell>
                      <span className="px-2 py-1 rounded-full text-sm bg-yellow-500/20 text-yellow-400">
                        {report.late}
                      </span>
                    </TableCell>
                    <TableCell>
                      <span className="px-2 py-1 rounded-full text-sm bg-green-500/20 text-green-400">
                        {report.onTime}
                      </span>
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </div>
        </Card>
      </main>
    </div>
  )
}
